/*global kakao*/
import React, { useEffect } from "react";
import { ReactComponent as MapTitle } from "images/map_title.svg";
import { ReactComponent as MapTitleSub } from "images/map_title_sub.svg";
function Location() {
  useEffect(() => {
    const s1 = document.createElement("script");
    s1.type = "text/javascript";
    s1.src =
      "https://dapi.kakao.com/v2/maps/sdk.js?appkey=aafac5abf9939148b3cc92475dde1380&libraries=services&autoload=false";
    document.head.appendChild(s1);
    s1.onload = () => {
      kakao.maps.load(() => {
        let container = document.getElementById("map");
        var position = new kakao.maps.LatLng(
          36.349287,
          127.438179
        );
        let options = {
          center: position,
          level: 4,
        };

        const map = new window.kakao.maps.Map(container, options);
        var imageSrc = require("../images/icon_location_mark.png"); // 마커이미지의 주소입니다
        var imageSize = new kakao.maps.Size(42, 52); // 마커이미지의 크기입니다
        var imageOption = { offset: new kakao.maps.Point(22, 49) }; // 마커이미지의 옵션입니다. 마커의 좌표와 일치시킬 이미지 안에서의 좌표를 설정합니다.

        // 마커의 이미지정보를 가지고 있는 마커이미지를 생성합니다
        var markerImage = new kakao.maps.MarkerImage(
          imageSrc,
          imageSize,
          imageOption
        );

        // 결과값으로 받은 위치를 마커로 표시합니다
        var marker = new kakao.maps.Marker({
          map: map,
          position: position,
          image: markerImage,
          clickable: true,
        });

        // 인포윈도우로 장소에 대한 설명을 표시합니다
        var customOverlay = new kakao.maps.CustomOverlay({
          position: position,
          content: `<div class="marker">선샤인웨딩홀</div>`,
          yAnchor: 3,
          xAnchor: 0.5,
        });
        // 커스텀 오버레이를 지도에 표시합니다
        customOverlay.setMap(map);
        kakao.maps.event.addListener(marker, "click", function () {
          // 마커 위에 인포윈도우를 표시합니다
          window.open("http://kko.to/lLZaTZr4p");
        });

        // 지도의 중심을 결과값으로 받은 위치로 이동시킵니다
        map.setCenter(position);
      });
    };
  }, []);
  return (
    <div className="location-container">
      <div className="graphic_title map">
        <h2>
          <MapTitle />
        </h2>
        <p className="sub map">
          <MapTitleSub />
        </p>
      </div>
      <div className="map-wrapper">
        <div id="map" style={{ width: "100%", height: "210px" }}></div>
      </div>
      <div className="info-box-wrapper">
        <div className="title">
          <h3>대전 동구 동서대로 1700</h3>
          <h3>선샤인웨딩홀 5층 그랜드볼룸</h3>
          <a href="tel:042-673-8800" className="tel">
            042-673-8800
          </a>
        </div>

		<div className="dic bus">
          <h3>시내버스</h3>
          <p>102, 105, 106, 201, 501, 601, 607, 611, 616, 701 802, 2(급행)</p>
        </div>

		<div className="dic exbus">
          <h3>고속/시외버스</h3>
          <p>도보로 10분 거리</p>
        </div>
		
		<div className="dic bus">
          <h3>자가용</h3>
          <p>대전 복합터미널 맞은편</p>
		</div>
		
        <div className="dic bus">
          <h3>주차</h3>
          <p>건물 내 주차장 1600대 가능</p>
		</div>
      </div>
    </div>
  );
}

export default Location;
